import React, { useEffect, useState } from 'react';
import { NextPage } from 'next';
import Head from 'next/head';
import DashboardLayout from '@/components/Dashboard/DashboardLayout';
import DashboardSidebar from '@/components/Dashboard/DashboardSidebar';
import CloudflareVideoPlayer from '@/components/Video/CloudflareVideoPlayer';
import VideoUploader from '@/components/Video/VideoUploader';
import Button from '@/components/Button';
import Link from 'next/link';
import videoService from '../api-connection/videos';

interface VideoEmbedInfo {
  uid: string;
  hls?: string;
  dash?: string;
  thumbnailUrl?: string;
  readyToStream?: boolean;
  status?: string;
}

const UploadVideoPage: NextPage = () => {
  const [uploading, setUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [uploadError, setUploadError] = useState<string | null>(null);
  const [videoId, setVideoId] = useState<string | null>(null);
  const [processing, setProcessing] = useState(false);
  const [videoReady, setVideoReady] = useState(false);
  const [videoEmbedInfo, setVideoEmbedInfo] = useState<{ hls: string; dash?: string } | null>(null);

  useEffect(() => {
    let interval: NodeJS.Timeout;
    
    // If we have a videoId and it's processing, check status every 2 seconds
    if (videoId && processing) {
      interval = setInterval(async () => {
        try {
          const response = await videoService.checkVideoStatus(videoId);
          if (response.success && response.video.readyToStream) {
            setProcessing(false);
            setVideoReady(true);
            setVideoEmbedInfo({
              hls: response.video.playback.hls,
              dash: response.video.playback.dash
            });
            clearInterval(interval);
          }
        } catch (error) {
          console.error('Error checking video status:', error);
        }
      }, 2000);
    }
    
    return () => {
      if (interval) clearInterval(interval);
    };
  }, [videoId, processing]);

  const handleUploadSuccess = (embedInfo: VideoEmbedInfo) => {
    setVideoId(embedInfo.uid);
    setUploading(false);
    
    if (embedInfo.readyToStream && embedInfo.hls) {
      setVideoReady(true);
      setVideoEmbedInfo({
        hls: embedInfo.hls,
        dash: embedInfo.dash
      });
    } else {
      setProcessing(true);
    }
  };

  const handleUploadError = (error: Error) => {
    setUploadError(error.message);
    setUploading(false);
  };

  const handleUploadProgress = (progress: number) => {
    setUploadProgress(progress);
  };

  return (
    <>
      <Head>
        <title>Upload Video</title>
      </Head>
      <DashboardLayout sidebar={<DashboardSidebar />}>
        <div className="p-4 md:p-6">
          <div className="mb-6 flex items-center">
            <Link href="/dashboard" passHref>
              <Button variant="outline" className="mr-4">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" />
                </svg>
                Back to Dashboard
              </Button>
            </Link>
            <h1 className="text-2xl font-bold">Upload Video</h1>
          </div>

          <div className="bg-white rounded-lg shadow-sm p-6 max-w-3xl">
            {!videoReady ? (
              <>
                {!processing ? (
                  <VideoUploader
                    maxDurationSeconds={3600}
                    onUploadSuccess={handleUploadSuccess}
                    onUploadError={handleUploadError}
                    onUploadProgress={handleUploadProgress}
                  />
                ) : (
                  <div className="text-center py-12">
                    <div className="inline-block animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500 mb-4"></div>
                    <h3 className="text-lg font-medium text-gray-900">Processing your video...</h3>
                    <p className="mt-2 text-sm text-gray-500">
                      This may take a few minutes depending on the size of your video.
                    </p>
                  </div>
                )}
              </>
            ) : (
              <div className="space-y-6">
                <h3 className="text-lg font-medium text-gray-900">Video uploaded successfully!</h3>
                
                {videoEmbedInfo && (
                  <div className="mt-6">
                    <CloudflareVideoPlayer
                      src={videoEmbedInfo}
                      title="Your uploaded video"
                      autoPlay={false}
                      className="mt-4"
                    />
                  </div>
                )}
                
                <div className="flex space-x-4">
                  <Link href="/my-videos" passHref>
                    <Button>Go to My Videos</Button>
                  </Link>
                  <Link href="/dashboard" passHref>
                    <Button variant="outline">Back to Dashboard</Button>
                  </Link>
                </div>
              </div>
            )}
          </div>
        </div>
      </DashboardLayout>
    </>
  );
};

export default UploadVideoPage; 